
import * as util from '../api/common'
import axios from 'axios'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'


export default {
components: {
XAddress, ChinaAddressV4Data
},
data () {
return {




step: 1, // 当前步数
allstep: 6, // 总步数
stepWidth: '16.6%', // 当前步宽
isOver: false, // 是否已经填完
currentUpImgName: '', // 当前上传图片类型的名称
imgs: [],
imgData: {
accept: 'image/gif, image/jpeg, image/png, image/jpg'
},
password2: '', // 确认密码
mobile: '', // 手机号
password: '', // 密码
business_agree_pic: '', // 营业许可正图
business_code_pic: '', // 营业执照注册号图片
sccial_agency_code_pic: '', // 社会信用机构代码证图片
content_person: '', // 联系人
agency_name: '', // 法人名
agency_mobile: '', // 法人手机号
province: '', // 省
city: '', // 市
area: '', // 区
agency_id_card: '', // 身份证号
brought_account: '', // 对公账号
brought_bank: '光大银行', // 对公银行
brought_other_bank: '', // 对公开户支行
company_name: '', // 公司全称
business_code: '', // 营业执照注册号
business_agree: '', // 营业许可正号
sccial_agency_code: '', // 社会信用机构代码证
addressData: [], // ChinaAddressV4Data
showAddress: false,
addressValue: [],
addressNames: '请选择所在地'

}
},
mounted () {
this.getCityinfo()
},
methods: {
next () {
var self = this
self.step = self.step + 1
var tempval = self.step / self.allstep
self.stepWidth = tempval * 100 + '%'
if (self.step >= self.allstep) {
self.isOver = true
}
},
next1 () { // 手机号
var self = this
if (self.mobile === '') {
self.$vux.toast.text('手机号不能为空')
return
}
let params = new URLSearchParams()
params.append('mobile', self.mobile)
var instance = axios.create({ baseURL: util.baseURL })

var url = 'shopeeker/mobile'
instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post(url, params).then(res => {
if (res.data.code === 200) {
console.log(res.data)
self.next()
} else {
console.log(res.data)
self.$vux.toast.text(res.data.message)
}
})
},
next2 () { // 密码
var self = this
if (self.password !== self.password2) {
self.$vux.toast.text('两次密码不一致！')
return
}
if (self.password === '' || self.password2 === '') {
self.$vux.toast.text('密码不能为空！')
return
}
self.next()
},
next3 () { // 资质认证
var self = this
if (self.company_name === '') {
self.$vux.toast.text('企业名称不能为空！')
return
} else if (self.business_code === '') {


} else if (self.sccial_agency_code === '') {
self.$vux.toast.text('社会信用机构代码证！')
return
} else if (self.business_agree === '') {
self.$vux.toast.text('旅行社经营许可证号！')
return
}
self.next()
},
next4 () { // 附件上传
var self = this
if (self.business_code_pic === '') {
self.$vux.toast.text('请上传营业执照！')
return
} else if (self.sccial_agency_code_pic === '') {
self.$vux.toast.text('请上传社会信用机构代码证！')
return
} else if (self.business_agree_pic === '') {
self.$vux.toast.text('请上传营业许可证！')
return
}
self.next()
},
next5 () { // 联系人
var self = this
if (self.content_person === '') {
self.$vux.toast.text('请填写联系人姓名！')
return
} else if (self.agency_mobile === '') {
self.$vux.toast.text('请填写联系人电话！')
return
} else if (self.province === '' || self.city === '' || self.area === '') {
self.$vux.toast.text('请填写所在地！')
return
}
self.next()
},
next6 () { // 完成
var self = this
if (self.agency_name === '') {
self.$vux.toast.text('请填写法人姓名！')
return
} else if (self.agency_id_card === '') {
self.$vux.toast.text('请填写法人身份证号！')
return
} else if (self.brought_account === '') {
self.$vux.toast.text('请填写对公账号！')
return
} else if (self.brought_bank === '') {
self.$vux.toast.text('请填写对应银行！')
return
} else if (self.business_agree_pic === '') {
self.$vux.toast.text('请填写对公开户支行！')
return
}

let params = new URLSearchParams()
params.append('mobile', self.mobile)
params.append('password', self.password)
params.append('business_agree_pic', self.business_agree_pic)
params.append('business_code_pic', self.business_code_pic)
params.append('sccial_agency_code_pic', self.sccial_agency_code_pic)
params.append('agency_name', self.agency_name)
params.append('agency_mobile', self.agency_mobile)
params.append('province', self.province)
params.append('city', self.city)
params.append('area', self.area)
params.append('agency_id_card', self.agency_id_card)
params.append('brought_account', self.brought_account)
params.append('brought_bank', self.brought_bank)
params.append('brought_other_bank', self.brought_other_bank)
params.append('company_name', self.company_name)
params.append('business_code', self.business_code)
params.append('business_agree', self.business_agree)
params.append('sccial_agency_code', self.sccial_agency_code)
params.append('content_person', self.content_person)

var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('shopeeker/register', params).then(res => {
if (res.data.code === 200) {
self.$vux.toast.text(res.data.message)
console.log(res.data)
setTimeout(function () {

self.$router.push('/login??userType=shopeeker')
}, 1500)
} else {
console.log(res.data)
self.$vux.toast.text(res.data.message)
}
})
},
openFile (currentUpImgName) {




const event = new MouseEvent('click')
this.$refs.file.dispatchEvent(event)
this.currentUpImgName = currentUpImgName
},
addImg (event) {
let url = util.baseURL + 'shopeeker/upload'
let self = this
let img1 = event.target.files[0]
let type = img1.type 
let size = img1.size 
if (self.imgData.accept.indexOf(type) === -1) {
alert('请选择我们支持的图片格式！')
return false
}
if (size > 3145728) {
alert('请选择3M以内的图片！')
return false
}
let file = new FormData()
file.append('file', img1)
file.append('type', type)

self.$axios.post(url, file, {
headers: {'Content-Type': 'multipart/form-data'}
}).then(res => {
console.log('上传结果：', res.data.pic)
switch (self.currentUpImgName) {
case 'business_agree_pic':
self.business_agree_pic = res.data.pic 
break
case 'business_code_pic':
self.business_code_pic = res.data.pic  
break
default:
self.sccial_agency_code_pic = res.data.pic 
}
self.clearfile()
})
},
delectImg (imgTypeName) {

var self = this
let params = new URLSearchParams()
switch (imgTypeName) {
case 'business_agree_pic':
params.append('file', self.business_agree_pic) 
break
case 'business_code_pic':
params.append('file', self.business_code_pic) 
break
default:
params.append('file', self.sccial_agency_code_pic) 
}
var instance = axios.create({ baseURL: util.baseURL })

const _this = this 
self.$vux.confirm.show({
content: '确定删除当前图片吗？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('shopeeker/fileDelete', params).then(res => {
if (res.data.code === 200) {
self.$vux.toast.text(res.data.message)
self.uploadImgUrl = ''
switch (imgTypeName) {
case 'business_agree_pic':
self.business_agree_pic = '' 
break
case 'business_code_pic':
self.business_code_pic = ''  
break
default:
self.sccial_agency_code_pic = '' 
}
}
})
}
})
},
showImgUrl (url) {
return util.imgShowURL + url
},
clearfile () {
this.$refs.file.value = ''
},
logHide (str) {
console.log('on-hide', str)
console.log(this.province)
console.log(this.city)
console.log(this.area)
},
logShow (str) {
console.log('on-show')
},
onShadowChange (ids, names) {
console.log(ids, names)
this.province = ids[0]
this.city = ids[1]
this.area = ids[2]
},
getCityinfo () {
var self = this
let params = new URLSearchParams()
var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('shopeeker/city', params).then(res => {
if (res.data.code === 200) {
console.log('地址信息：', res.data.data[100])
console.log('微信地址：', ChinaAddressV4Data[100])
self.addressData = res.data.data
} else {
console.log(res.data)
self.$vux.toast.text(res.data.message)
}
})
}
}
}



